/*******************************************************************************
 * SD_ApexCode generator Class
 ******************************************************************************/
var SD_ApexCode = function(options){
  
}
// quickApexCode Callback
// options : {fields : [], values : [], sobject : {}}
SD_ApexCode.prototype.generateApexCode = function(options) {
  var self = this;
  var fields = options.fields;
  var values = options.values;
  var sobject = options.sobject;
  var objName = sobject.name;
  if(T_Util.isBlank(objName)) return alert('sObject ERROR!');
  var fieldMap = {};
  for( var i = 0; i < fields.length; i++ ) {
    var f = fields[i];
    fieldMap[f.name] = f;
  }
  var apexCode = objName + ' sobj = new ' + objName + '(';
  var apexCodeFull = apexCode;
  var filedCodes = [];
  var labels = [];
  var maxLength = 0;
  var noBlankTotalLine = 0;
  for(var i = 0; i < values.length; i++){
    var val = values[i];
    var value = val.value;
    if(T_Util.isBlank(value)) value = ' ';
    value = value.toString().rtrim();
    if(T_Util.isBlank(val.name) || !fieldMap.hasOwnProperty(val.name)) continue;
    if(val.name == 'CreatedById' || val.name == 'LastModifiedById' || val.name == 'OwnerId') continue;
    var field = fieldMap[val.name];
    var fCode = self.getFieldCode(val, field);
    if(fCode == null) continue;
    var code = '\n  ' + val.name + ' = ' + fCode;

    if(!T_Util.isBlank(value)) noBlankTotalLine++;
    filedCodes.push({ code : code, isBlank : T_Util.isBlank(value) });
    labels.push('// '+ val.label);
    maxLength = (code.length > maxLength) ? code.length : maxLength;
  }
  //log('>>>> noBlankTotalLine ' + noBlankTotalLine);
  var noBlankIndex = 0;
  for(var i = 0; i < filedCodes.length; i++){
    var filedCode = filedCodes[i];
    var code = filedCode.code;
    var space = T_Util.getSpaceByNum(maxLength - code.length);
    //log(' maxLength - code.length ' + (maxLength - code.length));
    var offset = (i == 0) ? '' : '  '; //首行以外多加两个空格
    if(!filedCode.isBlank){
      noBlankIndex++;
      var comma = (noBlankIndex == noBlankTotalLine) ? '' : ',';
      apexCode +=  code + comma + offset + space + labels[i];
    }
    var comma = (i == (filedCodes.length - 1)) ? '' : ',';
    apexCodeFull += code + comma + offset + space + labels[i];
  }
  apexCode += '\n);\ninsert sobj;'
  apexCodeFull += '\n);\ninsert sobj;'
  var editor = '<pre id="quick-apex-editor" class="editor" readOnly="true">' + apexCode + '</pre>';
     editor += '<pre id="quick-apex-editor-full" class="editor hide" readOnly="true">' + apexCodeFull + '</pre>';
  editor = editor + '<div class="section-footer"><div class="checkbox checkbox-primary"><input type="checkbox" id="chk-blank-fields" value="1" name="all-fields" /><label for="chk-blank-fields">&nbsp;Include blank fields</label></div></div>';
  $('#container-quick-apex-code').empty().append(editor);
  self.initEditor('quick-apex-editor', 'java');
  self.initEditor('quick-apex-editor-full', 'java');

  //<input type="checkbox" '+checked+' value="'+sobject.name+'" id="chk_'+sobject.name+'"><label for="chk_'+sobject.name+'">&nbsp;</label></div>
}

SD_ApexCode.prototype.getFieldCode = function(val, field) {
  //console.log('>>>>  label ' + field.label + '-', val);
  var fieldType = field.type;
  if(T_Util.isBlank(fieldType)) return null;
  if(field.createable == false) return null;
  if(fieldType == 'formula' || field.autoNumber == true) return null;

  var value = val.value;
  if(T_Util.isBlank(value)) value = ' ';
  value = value.toString().rtrim();
  if(T_Util.isBlank(value)) return 'null';
  //console.log('>>>>  fieldType ' + field.label + '-', field, val);
  if(fieldType == 'recordtype'){
    value = value.replace(/\[(.*)\]/g,'').rtrim();
    if(field.recordTypeInfos.length == 0) return null;
    for(var i = 0; i < field.recordTypeInfos.length; i++){
      var rt = field.recordTypeInfos[i];
      // TODO RecordType Label
      if(value != rt.name) continue;
      return '\'' + rt.recordTypeId + '\'';
    }
    return null;
  }
  if(fieldType == 'currency' 
      || fieldType == 'number'
      || fieldType == 'percent') {
      value = value.replace(/\,/g,'').replace(/\%/g,'');
    return T_Util.getNumber(value);
  }
  if(fieldType == 'boolean') {
    if( value == true || value == 'true' || value == 1 || value == '1' )
      return 'true';
    else 
      return 'false';
  }
  if(fieldType == 'date') {
    return 'Date.valueOf(\'' + value.replace(/\//g,'-') + '\')';
  }
  if(fieldType == 'datetime') {
    return 'Datetime.valueOf(\'' + value.replace(/\//g,'-') + ':00\')';
  }
  if(fieldType == 'reference') {
    // TODO Recordtype undefined
    return '\'' + val.reference + '\'';
  }
  return '\'' + T_Util.punescape(value) + '\''
}

/** Init Editor */
SD_ApexCode.prototype.initEditor = function(ele, mode) {
  //var ele = this;
  if(typeof ace == 'undefined') return;
  mode = mode || 'java';
  var editor = ace.edit(ele);
  editor.setTheme("ace/theme/chrome");
  editor.getSession().setMode('ace/mode/' + mode);
  editor.setOptions({
    maxLines: Infinity,
    enableBasicAutocompletion: true,
    enableLiveAutocompletion: true
  });
  if(mode == 'sql'){
    // SOQL 自动换行、可编辑
    editor.getSession().setUseWrapMode(true);
  } else {
    editor.setReadOnly(true);
  }
  
  editor.renderer.setShowGutter(false);
  editor.$blockScrolling = Infinity;
  
  return editor;
}
