/*******************************************************************************
 * SD_Popup Class
 ******************************************************************************/
var SD_Popup = function(options){
  options = options || {};
  this.view = new SD_PopupView();
  this.pageCallback = null;
  this.tabid = null;
  
  this.debugmode = options.debugmode || false;
  this.pageInfo = null;
  this.metadata = null;
  this.debuglog = null;
}
/** Run App */
SD_Popup.prototype.run = function() {
  var self = this;
  self.sendRequest({ mode : 'init' }, function(res) {
    self.pageInfo = res;
    self.metadata = res.metadata;
    self.log('[init callback]', res);
    var isAuthed = self.showMainPage(res);
    if(isAuthed == false) {
      self.initOAuthListener();
    } else {
      self.initListener();
    }
  });
}
// Handle CROB request from content script
SD_Popup.prototype.handleCrobRequest = function(options, sendResponse) {
  var conn = new jsforce.Connection({
    version : SF_API_VERSION || '53.0',
    instanceUrl : options.instanceUrl,
    accessToken : options.accessToken
  });
  var callback = function(err, res) {
    chrome.tabs.executeScript(options.tabid, {
      code: 'T_App.callbackCrobRequest(' + JSON.stringify({ uuid : options.uuid, err : err, res : res }) + ');'
    }, function() {
      if (chrome.runtime.lastError) return self.handleError(chrome.runtime.lastError);
    });
  };
  // 动态调用会引发jsforce logger错误
  if(options.method == 'query') conn.query(options.params, callback);
  if(options.method == 'requestGet') conn.requestGet(options.params.url, callback);
  if(options.method == 'describe') conn.describe(options.params, callback);
  if(options.method == 'describeGlobal') conn.describeGlobal(callback);
  if(options.method == 'describeSObjects') conn.soap._invoke("describeSObjects", options.params, [{}], callback);
  if(options.method == 'deleteMetadata') conn.metadata.delete(options.params.metaType, options.params.fullNames, callback);
  if(options.method == 'readMetadata') conn.metadata.read(options.params.metaType, options.params.fullNames, callback);
  if(options.method == 'createMetadata') conn.metadata.create(options.params.metaType, options.params.metadata, callback);
  if(options.method == 'updateMetadata') conn.metadata.update(options.params.metaType, options.params.metadata, callback);
  if(options.method == 'renameMetadata') {
    conn.metadata.rename(options.params.metaType, options.params.oldFullName, options.params.newFullName, callback);
  }
  if(options.method == 'getLayouts') conn.requestGet(options.params.url, callback);
  if(options.method == 'listLayouts') conn.metadata.list([{type: 'Layout'}], conn.version, callback);
  if(options.method == 'listProfiles') conn.metadata.list([{type: 'Profile'}], conn.version, callback);
  if(options.method == 'getListViews') conn.requestGet(options.params.url, callback);
  if(options.method == 'getObjects') conn.metadata.read('CustomObject', options.params, callback);
  if(options.method == 'getProfiles') conn.metadata.read('Profile', options.params, callback);
  if(options.method == 'getWorkflows') conn.metadata.read('Workflow', options.params, callback);
  if(options.method == 'describeListView') conn.requestGet(options.params.url, callback);
  if(options.method == 'tooling.query') conn.tooling.query(options.params, callback);
  if(options.method == 'tooling.request') conn.tooling.request(options.params).thenCall(callback);
  if(options.method == 'oauth2.refreshToken') {
    conn = new jsforce.Connection({
      version : SF_API_VERSION || '53.0',
      oauth2 : options.params,
      instanceUrl : options.instanceUrl,
      accessToken : options.accessToken,
      refreshToken : options.params.refreshToken
    });
    conn.oauth2.refreshToken(options.params.refreshToken, callback);
  }
  
  //conn._logger._logLevel = 1;
  return sendResponse(true);
}
/*-----------------------------------------------------------------*
 * Private Functions
 *-----------------------------------------------------------------*/
// Send request to page 
SD_Popup.prototype.sendRequest = function(options, callback) {
  var self = this;
  self.log('[sendRequest]', options);
  self.getActiveTab(function(tab) {
    if(tab == null) return self.handleError('Tab not found');
    try{
      self.tabid = tab.id;
      options['tabid'] = self.tabid;
      self.pageCallback = { mode : options.mode, callback : callback };
      chrome.tabs.executeScript(tab.id, {
        code: 'T_App.handle(' + JSON.stringify(options) + ');'
      }, function() {
        if (chrome.runtime.lastError) return self.handleError(chrome.runtime.lastError);
      });
    } catch (error){
      return self.handleError(error);
    }
  });
}
// Handle request from page 
SD_Popup.prototype.handle = function(request) {
  var self = this;
  if(self.pageCallback == null) return;
  if(self.pageCallback.mode != request.mode) return;
  if(typeof self.pageCallback.callback != 'function') return;
  var extensionId = chrome.runtime.id;
  if(request.extensionId !== extensionId) return;
  self.log('[handle]', request);
  self.pageCallback.callback(request);
  // distory
  self.pageCallback = null;
}

// Query active tab 
SD_Popup.prototype.getActiveTab = function(callback) {
  chrome.tabs.query({active : true, currentWindow: true}, function (tabs) {
    if(T_Util.isBlank(tabs[0])) return callback(null);
    callback(tabs[0]);
  }); // .chrome.tabs.query
}

// init OAuth button event
SD_Popup.prototype.initOAuthListener = function() {
  var self = this;
  $('#btn-oauth').click(function(ev) {
    self.sendRequest({ mode : 'oauth' }, function(res) {
      self.view.hideUnauthedPage();
      self.run();
    });
  });
}

// init buttons event
SD_Popup.prototype.initListener = function() {
  var self = this;
  var isLEX = self.pageInfo.isLEX || false;
  var gaCategory = (isLEX) ? 'Lightning' : 'Classic';
  // Show API Name
  $('#btn-show-apiname').off().click(function(ev) {
    $(this).startLoading();
    self.sendRequest({ mode : 'showApiName' }, function(res) {
      self.view.showApiName();
    });
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'apiname', label : 'Show API Name' });
  });
  $('#btn-hide-apiname').off().click(function(ev) {
    $(this).startLoading();
    self.sendRequest({ mode : 'hideApiName' }, function(res) {
      self.view.hideApiName();
    });
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'apiname', label : 'Hide API Name' });
  });
  // Apex Code generator
  $('#btn-quick-apexcode').off().click(function(ev) {
    self.view.showQuickApexCodePage({ 
      returnCallback : function() {
        self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
      } 
    });
    self.sendRequest({ mode : 'resizePanel', height : 170 });
    self.sendRequest({ mode : 'getFieldValues' }, function(res) {
      var editor = new SD_ApexCode();
      editor.generateApexCode(res);
      // To wait ace editor done
      setTimeout(function(){
        self.sendRequest({ mode : 'resizePanel', height : self.view.getApexCodeHeight() });
      }, 300); 
      $('#chk-blank-fields').click(function() {
        self.view.toggleApexEditor($(this));
        setTimeout(function(){
          self.sendRequest({ mode : 'resizePanel', height : self.view.getApexCodeHeight() });
        }, 300); 
      });
    });
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'apexcode', label : 'ApexCode Generate' });
  });
  // Soql Generator
  $('#btn-quick-soql').off().click(function(ev) {
    self.showSoqlEditor();
  });
  // Field Editor
  $('#btn-field-editor').off().click(function(ev) {
    self.showFieldEditor({ sobject : self.pageInfo.sobject.name });
  });
  // Reload Metadata
  $('#btn-reload-meta').off().click(function(ev) {
    var $btn = $(this);
    $btn.startLoading();
    self.sendRequest({ mode : 'reloadMetadata' }, function(res) {
      $btn.stopLoading();
      if(res.done) {
        self.metadata = res.metadata;
        // Show done icon
        var iconHtml = $btn.html();
        $btn.html('<i class="fa fa-check-circle" />');
        setTimeout(function(){ $btn.html(iconHtml); }, 1500);
      }
    }); // Reload Metadata end
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'metadata', label : 'Reload Metadata' });
  });

  // Search Object Panel
  $('#btn-objects-panel').off().click(function(ev) {
    self.view.showSfdcObjectPage({ 
      returnCallback : function() {
        self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
      } 
    });
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Search Object' });
  });
  $('#btn-search-clear').off().click(function(ev) {
    $(this).siblings('input[type="text"]').val('').focus();
  });
  $('#chk-all-objects').off().click(function(ev) {
    self.view.checkAll();
  });
  $('#ipt-search-object').off().bind("keyup focus", function(event){
    var $ipt = $(this);
    var keyword = $ipt.val();
    //log('>>>> keyword ' + keyword);
    if(T_Util.isBlank(keyword)){
      //输入内容为空
      self.view.sfdcObjectNoResult();
      return self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchObjectHeight() });
    }
    var results = self.filterSobjectFromMeta(keyword);
    if(results.length == 0){
      self.view.sfdcObjectNotFound();
      return self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchObjectHeight() });
    }
    //log('>>>> results ' + results.length);
    self.view.showSfdcObjectResult({ results : results, salesforceURL : self.pageInfo.userInfo.salesforceURL, isLEX : self.pageInfo.isLEX, url : self.pageInfo.url });
    $('#container-sfdc-objects').find('.btn-object-query-editor').bind('click', function() {
      var sObjectName = $(this).data('name');
      self.showSoqlEditor({ sobject : sObjectName });
    });
    $('#container-sfdc-objects').find('.btn-object-field-editor').bind('click', function() {
      var sObjectName = $(this).data('name');
      self.showFieldEditor({ sobject : sObjectName });
    });
    return self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchObjectHeight() });
  });

  var _exportObjectDefinition = function(ev, $btn, sObjects) {
    $btn.startLoading();
    self.sendRequest({ mode : 'describeObjectFull', sObjects : sObjects }, function(res) {
      var docs = new SD_DocGenerator({ 
        metadata : self.metadata,
        language : self.pageInfo.userInfo.language 
      });
      docs.exportObjectDoc({ 
        describes : res.describes, 
        profiles : res.profiles, 
        objects : res.objects, 
        triggers : res.triggers, 
        workflows : res.workflows, 
        callback : function(){
          $btn.stopLoading();
          //$('#btn-export-dropdown').dropdown('toggle');
        } // end of callback
      });
    });
    ev.stopPropagation();
  }

  var _exportObjectFields = function(ev, $btn, sObjects) {
    $btn.startLoading();
    self.sendRequest({ mode : 'describeSObjects', sObjects : sObjects }, function(res) {
      var docs = new SD_DocGenerator({ 
        metadata : self.metadata,
        language : self.pageInfo.userInfo.language 
      });
      docs.exportObjectFieldsDoc({ describes : res.describes, 
        callback : function(){
          $btn.stopLoading();
          //$('#btn-export-dropdown').dropdown('toggle');
        } // end of callback
      });
    });
    ev.stopPropagation();
  }
  var _exportObjectLayout = function(ev, $btn, sObjects) {
    $btn.startLoading();
    self.sendRequest({ mode : 'requestPagelayout', sObjects : sObjects }, function(res) {
      var docs = new SD_DocGenerator({ 
        metadata : self.metadata,
        language : self.pageInfo.userInfo.language 
      });
      docs.exportObjectLayout({ sObjects : sObjects, layouts : res.layouts, 
        layoutMeta : res.layoutMeta, describes : res.describes,
        callback : function(){
          $btn.stopLoading();
        }  // end of callback
      });
    });
    ev.stopPropagation();
  }
  // Reqest listview metadata
  var _exportListView = function(ev, $btn, sObjects) {
    $btn.startLoading();
    self.sendRequest({ mode : 'requestListView', sObjects : sObjects }, function(res) {
      var docs = new SD_DocGenerator({ 
        metadata : self.metadata,
        language : self.pageInfo.userInfo.language 
      });
      docs.exportListView({ sObjects : sObjects, listviews : res.listviews, describes : res.describes,
        callback : function(){
          $btn.stopLoading();
        }  // end of callback
      });
    });
    ev.stopPropagation();
  }
  // Export all object definition (FULL)
  $('#btn-object-export-all').off().click(function(ev) {
    if(self.view.selectedObjects.length == 0) return;
    _exportObjectDefinition(ev, $(this), self.view.selectedObjects);
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export All Objects Definiton' });
  });
  // Export all object fields API Reference
  $('#btn-object-export-fields-all').off().click(function(ev) {
    if(self.view.selectedObjects.length == 0) return;
    _exportObjectFields(ev, $(this), self.view.selectedObjects);
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export All Object Fields Definition' });
  });
  // Export all page layout
  $('#btn-object-export-layout-all').off().click(function(ev) {
    if(self.view.selectedObjects.length == 0) return;
    _exportObjectLayout(ev, $(this), self.view.selectedObjects);
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export All Pagelayout' });
  });
  // Export all listview
  $('#btn-object-export-listview-all').off().click(function(ev) {
    if(self.view.selectedObjects.length == 0) return;
    _exportListView(ev, $(this), self.view.selectedObjects);
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export All Listview' });
  });
  // Export Single object API Reference
  if(self.pageInfo.page == 'detail' || self.pageInfo.page == 'list' || self.pageInfo.page == 'setting') {
    $('#btn-object-export').off().click(function(ev) {
      _exportObjectDefinition(ev, $(this), [self.pageInfo.sobject.name]);
      // GA
      T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export Object Definition' });
    });
    $('#btn-object-export-fields').off().click(function(ev) {
      _exportObjectFields(ev, $(this), [self.pageInfo.sobject.name]);
      // GA
      T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export Object Fields Definition' });
    });
    // Export page layout
    $('#btn-object-export-layout').off().click(function(ev) {
      _exportObjectLayout(ev, $(this), [self.pageInfo.sobject.name]);
      // GA
      T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export Pagelayout' });
    });
    // Export listview
    $('#btn-object-export-listview').off().click(function(ev) {
      _exportListView(ev, $(this), [self.pageInfo.sobject.name]);
      // GA
      T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export Listview' });
    });
  }
  
	// ER図生成
  $('#btn-object-export-erd').off().click(function(ev) {
    if(self.view.selectedObjects.length == 0) return;
    $(this).startLoading();
    self.sendRequest({ mode : 'describeSObjects', sObjects : self.view.selectedObjects }, function(res) {
      var docs = new SD_DocGenerator({ 
        metadata : self.metadata,
        language : self.pageInfo.userInfo.language 
      });
      docs.exportER({ describes : res.describes });
    });
    ev.stopPropagation();
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'search', label : 'Export ERD' });
  });

  // DebugLog viewer视图
  $('#btn-debuglog-viewer').off().click(function(ev) {
    self.view.showDebugLogViewPage({ 
      returnCallback : function() {
        self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
      } 
    });
    self.sendRequest({ mode : 'resizePanel', height : self.view.getLogViewerHeight() });
    T_Util.gaTrack({ category : gaCategory, action : 'debuglog', label : 'DebugLog Viewer' });
  });
  // DebugLog search
  $('#btn-debuglog-panel').off().click(function(ev) {
    self.openSearchDebuglog();
    T_Util.gaTrack({ category : gaCategory, action : 'debuglog', label : 'Search DebugLog' });
  });
  // DebugLog search input
  $('#ipt-search-debuglog').off().bind("keyup focus", function(event){
    var $ipt = $(this);
    var keyword = $ipt.val();
    //console.log('>>>> keyword ' + keyword);
    var results = self.filterDebugLog(keyword);
    self.view.showDebugLogResult({ results : results, salesforceURL : self.pageInfo.userInfo.salesforceURL, isLEX : self.pageInfo.isLEX, url : self.pageInfo.url });
    return self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchLogHeight() });
  });

  $('#btn-debuglog-reload').off().click(function(event){
    var $btn = $(this);
    $btn.startLoading();
    var existIds = Object.keys(self.debuglog);
    self.sendRequest({ mode : 'requestDebuglog', resize : false, existIds : existIds }, function(res) {
      $btn.stopLoading();
      var newLogIds = Object.keys(res.data);
      for(var i = 0; i < newLogIds.length; i++) {
        var logId = newLogIds[i];
        self.debuglog[logId] = res.data[logId];
      }
      $('#ipt-search-debuglog').focus();
      if(res.done) {
        // Show done icon
        var iconHtml = $btn.html();
        $btn.html('<i class="fa fa-check-circle" />');
        setTimeout(function(){ $btn.html(iconHtml); }, 1500);
      }
    }); // Reload Metadata end
    // GA
    T_Util.gaTrack({ category : gaCategory, action : 'debuglog', label : 'Reload DebugLog' });
  });

  $('[data-toggle="tooltip"]').tooltip();
  $('[data-toggle="dropdown"]').dropdown();

  $(document).keydown(function(e) {
    if (e.keyCode == 27) {
      self.sendRequest({ mode : 'hidePanel', height : 170 });
    }
  });
}
// @return Boolean - main page is showed
SD_Popup.prototype.showMainPage = function(options){
  var self = this;
  self.log('>>>> showMainPage', options);
  var self = this;
  if(options.page == 'unauth') {
    self.view.showUnauthedPage();
    self.sendRequest({ mode : 'resizePanel', height : self.view.getMessageHeight() });
    return false;
  }
  if(options.page == 'detail') {
    self.view.showDetailPage(options);
    self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
    return true;
  }
  if(options.page == 'list' || options.page == 'setting') {
    self.view.showListPage(options);
    self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
    return true;
  }
  if(options.page == 'debugloglist') {
    return self.openSearchDebuglog();
  }
  if(options.page == 'debuglogdetail') {
    options['sendRequest'] = function(opts){
      self.sendRequest(opts);
    }
    self.view.showLogViewer(options);
    return self.sendRequest({ mode : 'resizePanel', height : self.view.getLogViewerHeight() });
  }
  if(options.page == 'other') {
    self.view.showDefaultPage();
    self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchObjectHeight() });
    return true;
  }
}

// 显示SOQL编辑器
// options : { sobject : 'Account' || null }
SD_Popup.prototype.showSoqlEditor = function(options){
  var self = this;
  var isLEX = self.pageInfo.isLEX || false;
  var gaCategory = (isLEX) ? 'Lightning' : 'Classic';
  options = options || {};
  self.view.showQuickSoqlPage({ 
    returnCallback : function() {
      self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
    } 
  });
  self.sendRequest({ mode : 'resizePanel', height : 170 });
  self.sendRequest({ mode : 'getFieldValues', sobject : options.sobject }, function(res) {
    if(self.pageInfo.page == 'detail') {
      res['id'] = self.pageInfo.id;
    }
    var editor = new SD_SoqlEditor({ metadata : self.metadata, client : self });
    editor.init(res);
    // To wait ace editor done
    setTimeout(function(){
      self.sendRequest({ mode : 'resizePanel', height : 'MAX' });
    }, 300); 
    // 载入Relation項目一覧、to wait resizePanel done
    setTimeout(function(){
      editor.requestChildFields(null, 1);
    }, 1000); 
    // Export all data to Excel
    var currentIndex = 0;
    var _exportNextPageData = function(result, callback) {
      self.sendRequest({ mode : 'requestGet', url : result.nextRecordsUrl }, function(res) {
        if(res.error) {
          return callback(false);
        }
        res['currentIndex'] = currentIndex;
        currentIndex += res.records.length;
        editor.handleQuery(res, function(params) {
          if(res.nextRecordsUrl) {
            // 再帰処理
            _exportNextPageData(res, callback);
          } else {
            callback(true);
          }
        });
      });
    }
    // Query and export
    $('#btn-quick-soql-query,#btn-quick-soql-export,#btn-quick-soql-export-all').click(function(e){
      e.stopPropagation();
      var $btn = $(this);
      $btn.startLoading();
      var isExportAllToExcel = ($btn.attr('id') == 'btn-quick-soql-export-all');
      self.view.restoreQuickSoql();
      var soql = editor.editor.session.getValue();
      self.sendRequest({ mode : 'query', soql : soql }, function(res) {
        res['target'] = $btn;
        if(res.records) currentIndex += res.records.length;
        editor.handleQuery(res, function(params) {
          if(params) {
            params['mode'] = 'openQueryViewer';
            params['height'] = 'MAX';
            self.sendRequest(params);
          }
          if(isExportAllToExcel && res.totalSize > 2000 && res.nextRecordsUrl) {
            // 2000件以上、全てダウンロード
            $btn.stopLoading();
            $('#btn-quick-soql-export').startLoading();
            $('#btn-quick-soql-export-dropdown').addClass('disabled');
            _exportNextPageData(res, function() {
              $('#btn-quick-soql-export').stopLoading();
              $('#btn-quick-soql-export-dropdown').removeClass('disabled');
            });
          } else {
            $btn.stopLoading();
          }
        });
      }); // query end

      if($btn.attr('id') == 'btn-quick-soql-query') {
        T_Util.gaTrack({ category : gaCategory, action : 'soqlquery', label : 'SOQL Query' });
      } else {
        T_Util.gaTrack({ category : gaCategory, action : 'soqlexport', label : 'SOQL Result Export' });
      }
    });
  });
  // GA
  T_Util.gaTrack({ category : gaCategory, action : 'soqleditor', label : 'Open SOQL Editor' });
}

// 搜索sObject
SD_Popup.prototype.filterSobjectFromMeta = function(keyword){
  var self = this;
  var results = [];
  keyword = (T_Util.isBlank(keyword)) ? '' : keyword;
  var lowcase = keyword.toLowerCase();
  var describes = self.metadata || [];
  for(var i = 0; i < describes.length; i++){
    var sobj = describes[i];
    if(T_Util.isBlank(sobj.name) || T_Util.isBlank(sobj.label)) continue;
    if (sobj.name.toLowerCase().indexOf(lowcase) >= 0) {
      results.push(sobj);
      // 超过45，导出excel会出错
      if(results.length >= 45) return results;
    } else if (sobj.label.toLowerCase().indexOf(lowcase) >= 0) {
      results.push(sobj);
      if(results.length >= 45) return results;
    }
  }
  return results;
}

// 显示Field编辑器
// options : { sobject : 'Account' || null }
SD_Popup.prototype.showFieldEditor = function(options){
  var self = this;
  var params = { sobject : options.sobject };
  params['mode'] = 'openFieldEditor';
  params['height'] = 'MAX';
  self.sendRequest(params);
}


// 显示DebugLog检索
SD_Popup.prototype.openSearchDebuglog = function(){
  var self = this;
  self.view.showDebugLogSearchPage({ 
    returnCallback : function() {
      self.sendRequest({ mode : 'resizePanel', height : self.view.getContainerHeight() });
    }
  });
  // Load debuglog data
  if(self.debuglog == null) {
    if(!$('#loading').hasClass('hide')){
      $('#loading').addClass('hide');
    }
    self.sendRequest({ mode : 'requestDebuglog', resize : true, height : self.view.getSearchLogHeight() }
      , function(res) {
      self.debuglog = res.data;
      $('#container-debuglog-search').find('.header').removeClass('hide');
      $('#container-debuglog-search').find('#debuglog-loading').addClass('hide');
      var results = self.filterDebugLog('');
      self.view.showDebugLogResult({ results : results, salesforceURL : self.pageInfo.userInfo.salesforceURL, isLEX : self.pageInfo.isLEX, url : self.pageInfo.url });
      self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchLogHeight() });
      $('#ipt-search-debuglog').focus();
    });
  } else {
    self.sendRequest({ mode : 'resizePanel', height : self.view.getSearchLogHeight() });
  }
}



// 搜索Debuglog
SD_Popup.prototype.filterDebugLog = function(keyword){
  var self = this;
  var results = [];
  keyword = (T_Util.isBlank(keyword)) ? '' : keyword;
  var lowcase = keyword.toLowerCase();
  var logIds = Object.keys(self.debuglog) || [];
  for(var i = 0; i < logIds.length; i++){
    var logId = logIds[i];
    var log = $.extend(true, {}, self.debuglog[logId]);
    var logBody = log.Body;
    if(T_Util.isBlank(keyword)) {
      log['Body'] = logBody.substr(0, 250);
      results.push(log);
      continue;
    }
    var index = logBody.toLowerCase().indexOf(lowcase);
    if(index < 0) continue;
    // Replace highlight tag
    var re = new RegExp(keyword, 'gi');
    var partBody = logBody.substr(index, 250);
    var lines = logBody.substr(0, index).split('\n');
    log['Body'] = partBody.replace(re, function(matched) {
      return '<span class="hl">' + matched + '</span>';
    });
    log['Line'] = lines.length;
    results.push(log);
  }
  return results;
}


// Handle error
SD_Popup.prototype.handleError = function(error) {
  console.error(error);
}

/** 
 * console.log
 * @param  String
 */
SD_Popup.prototype.log = function(){
  if(this.debugmode){
    console.log('[DEBUG]' + ((new Date()).toString()), arguments);
  }
}