// {layouts: Array, sObjects: Array}
SD_DocGenerator.prototype.exportObjectLayout = function(options) {
  //console.log('>>>>> exportObjectLayout ', options);
  var self = this;
  options = options || {};

  // format object field metadata map
  var describes = options.describes || [];
  describes = Array.isArray(describes) ? describes : [describes];
  var objFieldMetaUpMap = {};  // { objectName.toUpperCase : { fieldName : meta } } }
  for(var i = 0; i < describes.length; i++) {
    var describe = describes[i];
    var fieldMap = {};
    for(var f = 0; f < describe.fields.length; f++) {
      var field = describe.fields[f];
      fieldMap[field.name.toUpperCase()] = field;
    }
    objFieldMetaUpMap[describe.name.toUpperCase()] = fieldMap;
  }

  // format layout id map
  var layoutFullNameMap = {};
  var layoutMeta = options.layoutMeta || [];
  for(var i = 0; i < layoutMeta.length; i++) {
    var lm = layoutMeta[i];
    layoutFullNameMap[lm.id] = lm.fullName;
  }

  //var targetObjects = options.sObjects || [];
  var layoutMetadata = options.layouts || [];
  layoutMetadata = Array.isArray(layoutMetadata) ? layoutMetadata : [layoutMetadata];
  // Sort by fullName
  layoutMetadata.sort(function(a, b){
    var comparison = 0;
    if (a.sobject > b.sobject) {
      comparison = 1;
    } else if (b.sobject > a.sobject) {
      comparison = -1;
    }
    return comparison;
  });

  var fieldTypesMap = self.getFieldTypesMap();
  
  //Reference Object Label
  var metas = self.metadata || [];
  var objLabelMap = {};
  for(var k in metas){
    var obj = metas[k];
    if(T_Util.isBlank(obj.name) || T_Util.isBlank(obj.label)) continue;
    objLabelMap[obj.name.toUpperCase()] = obj.label;
  }
  
  //log('>>>> callbackFieldMetadata ' + JSON.stringify(describes.length));
  var excelData = [];
  var sheetNames = [];
  // Outline
  var outlineSheet = (layoutMetadata.length == 1) ? null : {};
  if(layoutMetadata.length > 1) {
    outlineSheet['rows'] = [];
    // TODO RecordType Id
    outlineSheet['rows'].push({
      cells : [
        {style : 'Header', value : 'NO', width : '7.5', 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['object'], width : '32.5'},
        {style : 'Header', value : fieldTypesMap['api_name'], width : '32.5'},
        {style : 'Header', value : fieldTypesMap['layout_name'], width : '64'},
        {style : 'Header', value : fieldTypesMap['recordtype'], width : '20'},
      ], height : '22.5'
    });
  }
  var layoutIds = [];
  for(var i = 0; i < layoutMetadata.length; i ++){
    var layout = layoutMetadata[i];
    var sobjectName = layout.sobject;
    var layoutName = layoutFullNameMap.hasOwnProperty(layout.id) ? decodeURIComponent(layoutFullNameMap[layout.id]) : layout.id;
    layoutName = layoutName.replace(sobjectName + '-', ''); // Account-Account Layout → Account Layout
    var sobjectLabel = objLabelMap[sobjectName.toUpperCase()];
    var recordTypeLabel = (layout.recordType) ? layout.recordType.name : fieldTypesMap['layout_master'];
    //var fieldMetaMap = objFieldMetaUpMap[sobjectName.toUpperCase()] || {};  // Field metadata data map 

    var sheet = {};
    sheet['rows'] = [];
    // Object Header
    sheet['rows'].push({
      cells : [
        {style : 'Header', value : fieldTypesMap['object'], width : '6.5', 'align' : 'center'},    // A
        {style : 'Header', marge : 'A1:B1', value : '', width : '28.5', 'align' : 'center'},        // B
        {style : 'Cell', value : sobjectLabel, width : '32.5'},        // C
        {style : 'Header', value : fieldTypesMap['recordtype'], 'align' : 'center'},  // D
        {style : 'Cell', value : recordTypeLabel},    // E
        {style : 'Cell', value : ''},    // F
        {style : 'Cell', value : ''},    // G
        {style : 'Cell', value : '', marge : 'E1:H1'},    // H
      ], height : '22.5'
    });
    sheet['rows'].push({
      cells : [
        {style : 'Header', value : fieldTypesMap['api_name'], 'align' : 'center'},    // A
        {style : 'Header', marge : 'A2:B2', value : '', 'align' : 'center'},        // B
        {style : 'Cell', value : sobjectName},        // C
        {style : 'Header', value : fieldTypesMap['layout_name'], 'align' : 'center'},  // D
        {style : 'Cell', value : layoutName},    // E
        {style : 'Cell', value : ''},    // F
        {style : 'Cell', value : ''},    // G
        {style : 'Cell', value : '', marge : 'E2:H2'},    // H
      ], height : '22.5'
    });
    // Blank Line
    sheet['rows'].push({cells : []});
    var secRow = 4;
    var fieldNo = 1;
    // Header
    sheet['rows'].push({
      cells : [
        {style : 'Header', value : 'NO', 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['label'], 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['api_name'], 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['data_type'], width : '28.5', 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['layout_position'], width : '6.5', 'align' : 'center'},
        {style : 'Header', marge : 'E'+secRow+':F'+secRow, 'align' : 'center', value : '', width : '6.5'},
        {style : 'Header', value : fieldTypesMap['layout_readonly'], width : '13', 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['required'], width : '9.5', 'align' : 'center'},
        {style : 'Header', value : fieldTypesMap['note'], width : '64', 'align' : 'center'},  // 備考
      ], height : '17'
    });
    // Edit Section
    var editLayoutSectionMap = {};
    for(var s = 0; s < layout.editLayoutSections.length; s++){
      var section = layout.editLayoutSections[s];
      editLayoutSectionMap[section.layoutSectionId] = section;
    }

    for(var s = 0; s < layout.detailLayoutSections.length; s++){
      // https://developer.salesforce.com/docs/atlas.ja-jp.api_meta.meta/api_meta/meta_layouts.htm
      var section = layout.detailLayoutSections[s];
      // Blank Line
      //sheet['rows'].push({cells : [],height : '5'});

      var remark = '';
      var sectionStyle = 'Section';
      //remark += section.columns + '-列';
      //if(section.tabOrder == 'TopToBottom') remark += '、タブ：上から下';
      //if(section.tabOrder == 'LeftToRight') remark += '、タブ：左から右';
      
      var editSection = editLayoutSectionMap[section.layoutSectionId];
      if(section.useHeading === false && !editLayoutSectionMap.hasOwnProperty(section.layoutSectionId)) {
        remark += fieldTypesMap['layout_header_not_visible'];
        sectionStyle = 'SectionGray';
      } else if(section.useHeading === false && editLayoutSectionMap.hasOwnProperty(section.layoutSectionId)) {
        remark += (editSection.useHeading === false) ? fieldTypesMap['layout_header_not_visible'] : fieldTypesMap['layout_header_edit_only'];
        sectionStyle = (editSection.useHeading === false) ? 'SectionGray' : 'SectionLight';
      } else if(section.useHeading === true && (!T_Util.isBlank(editSection) && editSection.useHeading === false)) {
        remark += fieldTypesMap['layout_header_detail_only'];
        sectionStyle = 'SectionLight';
      }
      if(remark != '') remark = ' (' + remark + ')';
       
      secRow++;
      // Section Header
      sheet['rows'].push({
        cells : [
          {style : sectionStyle, value : section.heading + remark},        // A
          {style : sectionStyle, value : ''},        // B
          {style : sectionStyle, value : ''},        // C
          {style : sectionStyle, value : ''},        // D
          {style : sectionStyle, value : ''},        // E
          {style : sectionStyle, value : ''},        // F
          {style : sectionStyle, value : ''},        // G
          {style : sectionStyle, value : ''},
          {style : sectionStyle, marge : 'A'+secRow+':I'+secRow, value : ''},  // 備考
        ], height : '14'
      });

      for(var r = 0; r < section.layoutRows.length; r++){
        var layoutRow = section.layoutRows[r];
        for(var c = 0; c < layoutRow.layoutItems.length; c++){
          var item = layoutRow.layoutItems[c];
          if(T_Util.isBlank(item)) continue;
          // TODO Multi Components
          var itemComponent = item.layoutComponents[0];
          // Blank item
          if(T_Util.isBlank(itemComponent)) continue;
          var fieldMeta = itemComponent.details;
          if(T_Util.isBlank(fieldMeta)) continue;
          if(fieldMeta.type == 'boolean') {
            fieldMeta.nillable = 'true';
          }
          secRow++;
          var label = item.label;
          // Field Type Label
          var fieldTypeLabel = self.getFieldType(fieldMeta, fieldTypesMap);
          if(fieldMeta && fieldMeta.type == 'reference' && !T_Util.isBlank(fieldMeta.referenceTo)) {
            var refProp = self.getFieldReferenceProp(fieldMeta, objLabelMap);
            fieldTypeLabel += ' ('+refProp.refLabels.join(',')+')';
          }
          var readOnlyLabel = (item.editableForNew && item.editableForUpdate) ? '' : fieldTypesMap['layout_readonly'];  // TODO Check
          var requiredLabel = (item.required) ? fieldTypesMap['required'] : ''; 
          var remarks = (fieldMeta.nillable == 'true' || fieldMeta.nillable == true) ? '' : fieldTypesMap['required_fields'];
          var margeColumn = (section.columns > 1) ? '' : 'E'+secRow+':F'+secRow;
          sheet['rows'].push({
            cells : [
              {style : 'Cell', value : fieldNo, 'align' : 'center'},        // A
              {style : 'Cell', value : label},   // B
              {style : 'Cell', value : itemComponent.value},        // C
              {style : 'Cell', value : fieldTypeLabel},        // D
              {style : 'Cell', value : (( c % 2 ) == 0) ? fieldTypesMap['check'] : '', 'align' : 'center'},        // E, Left Column
              {style : 'Cell', value : (( c % 2 ) != 0) ? fieldTypesMap['check'] : '', 'align' : 'center', marge : margeColumn},        // F, Right Column
              {style : 'Cell', value : readOnlyLabel},        // G, 参照のみ
              {style : 'Cell', value : requiredLabel},       // H, 必須
              {style : 'Cell', value : remarks},  // 備考
            ]
          });
  
          fieldNo++;
        }
      }
      


    } // end section loop

    // Buttons
    if(layout.buttonLayoutSection && layout.buttonLayoutSection.detailButtons.length > 0) {
      var detailButtons = self.getLayoutDetailButtons(layout.buttonLayoutSection.detailButtons, fieldTypesMap['custom']);
      secRow = secRow + 2;
      sheet['rows'].push({cells : []});
      sheet['rows'].push({
        cells : [{style : 'H2', marge : 'A'+secRow+':I'+secRow, value : fieldTypesMap['layout_section_button'], 'align' : 'left'}]
      });
      secRow = secRow + 2;
      //var heightVal = Math.ceil(detailButtons.length / 10) * 14;  // TODO , wrapText : true
      var heightVal = 14;
      sheet['rows'].push({cells : [],height : '5'});
      sheet['rows'].push({
        cells : [
          {style : 'Buttons', value : '　' + detailButtons.join(' | '), 'align' : 'left'},        // A
          {style : 'Buttons', value : ''},        // B
          {style : 'Buttons', value : ''},        // C
          {style : 'Buttons', value : ''},        // D
          {style : 'Buttons', value : ''},        // E
          {style : 'Buttons', value : ''},        // F
          {style : 'Buttons', value : ''},        // G
          {style : 'Buttons', value : ''},        // H
          {style : 'Buttons', marge : 'A'+secRow+':I'+secRow, value : '', 'align' : 'left'},  // 備考
        ], height : '' + heightVal
      });
    } 

    // Quick Actions
    /*var quickActions = self.getLayoutQuickActions(layout.quickActionList.quickActionListItems);
    if(quickActions.length > 0) {
      secRow = secRow + 2;
      sheet['rows'].push({cells : []});
      sheet['rows'].push({
        cells : [{style : 'H2', marge : 'A'+secRow+':I'+secRow, value : 'Quick Actions in the Salesforce Classic Publisher', 'align' : 'left'}]
      });
      secRow = secRow + 2;
      sheet['rows'].push({cells : [],height : '5'});
      sheet['rows'].push({
        cells : [
          {style : 'Buttons', value : quickActions.join('  '), 'align' : 'left'},        // A
          {style : 'Buttons', value : ''},        // B
          {style : 'Buttons', value : ''},        // C
          {style : 'Buttons', value : ''},        // D
          {style : 'Buttons', value : ''},        // E
          {style : 'Buttons', value : ''},        // F
          {style : 'Buttons', value : ''},        // G
          {style : 'Buttons', value : ''},        // H
          {style : 'Buttons', marge : 'A'+secRow+':I'+secRow, value : '', 'align' : 'left'},  // 備考
        ], height : '14'
      });
    } */

    // Related List
    if(layout.relatedLists.length > 0) {
      secRow = secRow + 2;
      sheet['rows'].push({cells : []});
      sheet['rows'].push({
        cells : [{style : 'H2', marge : 'A'+secRow+':I'+secRow, value : fieldTypesMap['layout_section_relatedlists'], 'align' : 'left'}]
      });
      secRow = secRow + 2;
      sheet['rows'].push({cells : [],height : '5'});
      // 56458c
      sheet['rows'].push({
        cells : [
          {style : 'RelateHeader', value : 'NO', 'align' : 'center'},
          {style : 'RelateHeader', value : fieldTypesMap['label'], 'align' : 'center'},
          {style : 'RelateHeader', value : fieldTypesMap['api_name'], 'align' : 'center'},
          {style : 'RelateHeader', value : fieldTypesMap['data_type'], 'align' : 'center'},
          {style : 'RelateHeader', value : fieldTypesMap['sort'], 'align' : 'center'},
          {style : 'RelateHeader', marge : 'E'+secRow+':F'+secRow, 'align' : 'center', value : ''},
          {style : 'RelateHeader', value : '', 'align' : 'center'},
          {style : 'RelateHeader', value : '', 'align' : 'center'},
          {style : 'RelateHeader', marge : 'G'+secRow+':I'+secRow, 'align' : 'center', value : fieldTypesMap['note']},  // 備考
        ], height : '17'
      });
    }

    for(var r = 0; r < layout.relatedLists.length; r++){
      secRow++;
      var relSection = layout.relatedLists[r];
      var relObjMeta = objFieldMetaUpMap[relSection.sobject.toUpperCase()];
      //console.log('>>>> relSection ', relSection , relObjMeta);
      // Blank Line
      //sheet['rows'].push({cells : [],height : '5'});
      // Section Header
      sheet['rows'].push({
        cells : [
          {style : 'RelateSectionLight', value : relSection.label + ' (' + relSection.sobject + ')'},        // A
          {style : 'RelateSectionLight', value : ''},        // B
          {style : 'RelateSectionLight', value : ''},        // C
          {style : 'RelateSectionLight', value : ''},        // D
          {style : 'RelateSectionLight', value : ''},        // E
          {style : 'RelateSectionLight', value : ''},        // F
          {style : 'RelateSectionLight', value : ''},        // G
          {style : 'RelateSectionLight', value : ''},        // H
          {style : 'RelateSectionLight', marge : 'A'+secRow+':I'+secRow, value : '', 'align' : 'left'},  // 備考
        ], height : '14'
      });
      //console.log('>>>>> Section Header A:I' + secRow);

      // Buttons
      if(relSection.buttons && relSection.buttons.length >= 1) {
        secRow++;
        var buttons = [];
        for(var b = 0; b < relSection.buttons.length; b++) {
          var button = relSection.buttons[b];
          buttons.push(button.label);
        }
        sheet['rows'].push({
          cells : [
            {style : 'Buttons', value : '　' + buttons.join(' | '), 'align' : 'left'},        // A
            {style : 'Buttons', value : ''},        // B
            {style : 'Buttons', value : ''},        // C
            {style : 'Buttons', value : ''},        // D
            {style : 'Buttons', value : ''},        // E
            {style : 'Buttons', value : ''},        // F
            {style : 'Buttons', value : ''},        // G
            {style : 'Buttons', value : ''},        // H
            {style : 'Buttons', marge : 'A'+secRow+':I'+secRow, value : '', 'align' : 'left'},  // 備考
          ], height : '14'
        });
      }

      // Sort
      var sortFieldMap = {};
      for(var so = 0; so < relSection.sort.length; so++) {
        var sort = relSection.sort[so];
        sortFieldMap[sort.column] = (sort.ascending) ? fieldTypesMap['asc'] :  fieldTypesMap['desc'];
      }

      if(T_Util.isBlank(relSection.columns)) continue;
      //console.log('>>>>> relSection.fields', relSection);
      var _pushField = function(fNo, rowNo, field) {
        //objFieldMetaUpMap
        var fieldMeta;
        var apiName = field.fieldApiName;
        if(relObjMeta.hasOwnProperty(apiName.toUpperCase())) {
          fieldMeta = relObjMeta[apiName.toUpperCase()];
        }
        // Field Type Label
        var fieldTypeLabel = self.getFieldType(fieldMeta, fieldTypesMap);
        if(fieldMeta && fieldMeta.type == 'reference' && !T_Util.isBlank(fieldMeta.referenceTo)) {
          var refProp = self.getFieldReferenceProp(fieldMeta, objLabelMap);
          fieldTypeLabel += ' ('+refProp.refLabels.join(',')+')';
        }
        // Who.Name
        if(T_Util.isBlank(fieldTypeLabel)) {
          var apiNames = apiName.split('.');
          if(apiNames.length == 2 && apiNames[1] == 'Name') {
            fieldTypeLabel = fieldTypesMap['string'];
          }
        }
        sheet['rows'].push({
          cells : [
            {style : 'Cell', value : fNo, 'align' : 'center'},        // A
            {style : 'Cell', value : field.label},        // B
            {style : 'Cell', value : apiName},        // C
            {style : 'Cell', value : fieldTypeLabel},        // D
            {style : 'Cell', value : (sortFieldMap.hasOwnProperty(apiName) ? sortFieldMap[apiName] : '' ), 'align' : 'left'},        // E
            {style : 'Cell', marge : 'E'+rowNo+':F'+rowNo, value : '', 'align' : 'left'},        // F
            {style : 'Cell', value : ''},        // G
            {style : 'Cell', value : ''},       // H
            {style : 'Cell', marge : 'G'+rowNo+':I'+rowNo, value : ''},  // 備考
          ]
        });
      }

      for(var f = 0; f < relSection.columns.length; f++){
        secRow++;
        _pushField(f+1, secRow, relSection.columns[f]);
        //console.log('>>>>> Section Field A:I' + secRow);
      } // .relSection.fields

    }
    // Outline
    var sobjectLabel = objLabelMap[sobjectName.toUpperCase()];

    if(outlineSheet != null) {
      outlineSheet['rows'].push({
        cells : [
          {style : 'No', value : (i+1), 'align' : 'center'},            //NO
          {style : 'Cell', value : sobjectLabel},    //Field Label
          {style : 'Cell', value : sobjectName},     //API Name
          {style : 'Cell', value : layoutName},     //Layout Name
          {style : 'Cell', value : recordTypeLabel}     //Master Layout
        ]
      });
      // : fieldTypesMap['standard']
    }
    excelData.push(sheet);
    sheetNames.push(layoutName);
  } // End of layout loop

  // 処理完了
  /*if($('#btn-object-export-layout').isLoading()) {
    $('#btn-object-export-layout').stopLoading();
  }*/
  if(outlineSheet != null) {
    // Insert to first
    excelData.unshift(outlineSheet);
    sheetNames.unshift('Layouts');
  }
  //console.log('>>>> excelData ' + JSON.stringify(excelData));
  //console.log('>>>> sheetNames ' + JSON.stringify(sheetNames));
  var fileName = 'Salesforce Page Layouts';
  if(sheetNames.length == 1) {
    //只有一个Object
    fileName = sheetNames;
  }
  // Fix name too long bug
  var newSheetNames = [];
  for(var i=0; i<sheetNames.length; i++) {
    var sheetName = sheetNames[i];
    if(sheetName.length > 30) {
      // 不能超过31
      var parts = sheetName.split('__');
      if(parts.length > 2){
        // Package Object
        sheetName = parts[(parts.length - 2)] + '__' + parts[(parts.length - 1)];
      }
      sheetName = sheetName.substr(0, 30);
    }
    sheetName = sheetName.replace(/(\/)|(\\)|(\[)|(\])|(\*)|(\:)|(\?)/g, '');
    if(newSheetNames.indexOf(sheetName) >= 0) {
      // Sheetname重複エラー
      sheetName = sheetName + '2';
    }
    newSheetNames.push(sheetName);
  }
  self.downloadExcel(excelData, newSheetNames, fileName);
  // 処理完了
  if(typeof options.callback == 'function') {
    options.callback();
  }
}

/********************************************************/
/*******************  生成Listview  *********************/
/********************************************************/

// {listviews: Array, sObjects: Array, describes : Array}
SD_DocGenerator.prototype.exportListView = function(options) {
  //console.log('>>>> exportListView', options)
  var self = this;
  options = options || {};
  var describes = options.describes || [];
  var listviews = options.listviews || {};
  var sObjectNames = options.sObjects || {};
  describes = Array.isArray(describes) ? describes : [describes];

  var fieldTypesMap = self.getFieldTypesMap();

  var objLabelMap = {};
  var metas = self.metadata || [];
  for(var k in metas){
    var obj = metas[k];
    if(T_Util.isBlank(obj.name) || T_Util.isBlank(obj.label)) continue;
    objLabelMap[obj.name.toUpperCase()] = obj.label;
  }

  //Object Field map
  var objFieldMap = {}; // { objectName : { fields { fieldName : describe }, label : '' } }
  for(var i = 0; i < describes.length; i++){
    var describe = describes[i];
    objFieldMap[describe.name] = {
      label : describe.label,
      fields : {}
    }
    for(var f = 0; f < describe.fields.length; f++) {
      var field = describe.fields[f];
      objFieldMap[describe.name].fields[field.name] = field;
      if(field.type == 'reference' && field.relationshipName) {
        objFieldMap[describe.name].fields[field.relationshipName] = field; // { Parent__r : {} }
      }
    }
  }
  // Get field describe from field name
  var _getFieldDesribe = function(sobjectName, fieldName) {
    if(!objFieldMap.hasOwnProperty(sobjectName)) return null;
    var fieldNames = fieldName.split('.');
    if(fieldNames.length == 1) {
      // sobject内的项目
      return { sobject : sobjectName, 
              label : objFieldMap[sobjectName].label, 
              describe : objFieldMap[sobjectName].fields[fieldName] };
    } else {
      // 親の項目
      var field = objFieldMap[sobjectName].fields[fieldNames[0]];
      if(T_Util.isBlank(field)) return null;
      var refObjFieldMap = objFieldMap[field.referenceTo];
      return {
        sobject : refObjFieldMap.name, 
        label : refObjFieldMap.label, 
        describe : refObjFieldMap.fields[fieldNames[1]] 
      }
    }
  } // end of _getFieldDesribe

  // 递推抽取Condition Object
  var _getConditons = function(condition, start) {
    var conditions = [];
    var conjunction = '';
    if(T_Util.isBlank(condition.conditions) && condition.field) {
      return { conditions : [condition], conjunction : 'AND' };
    } else if(condition.conditions.length == 0) {
      return { conditions : [], conjunction : (condition.conjunction || 'AND').toUpperCase() };
    }
    var conjIndexs = [];
    for(var i = 0; i < condition.conditions.length; i++) {
      var cond = condition.conditions[i];
      if(cond.field) {
        if(condition.conjunction == 'or') cond['conjunction'] = condition.conjunction.toUpperCase();  // OR行涂色用
        conjIndexs.push(start + i);
        conditions.push(cond);
      } else if(Array.isArray(cond.conditions)){
        // 递推
        var cIndex = (start + i);
        var childCondition = _getConditons(cond, cIndex);
        //console.log('>>>>> childCondition', childCondition);
        conjIndexs.push('( ' + childCondition.conjunction + ' )');
        conditions = conditions.concat(childCondition.conditions);
      }
    }
    conjunction = conjIndexs.join(' ' + condition.conjunction.toUpperCase() + ' ') ;
    return { conditions : conditions, conjunction : conjunction };
  }
  
  var _outputListViewRows = function(sobjectName, listview, startRow) {
    var secRow = startRow;
    var rows = [];
    // Object Header
    rows.push({
      cells : [
        {style : 'Header', value : fieldTypesMap['listview_label'], 'align' : 'center'},
        {style : 'Header', marge : 'A'+secRow+':B'+secRow, value : '', 'align' : 'center'},
        {style : 'Cell', value : listview.label},
        {style : 'Header', value : fieldTypesMap['listview_apiname'], 'align' : 'center'},
        {style : 'Header', marge : 'D'+secRow+':E'+secRow, value : '', 'align' : 'center'},
        {style : 'Cell', value : listview.developerName},
      ], height : '17'
    });
    rows.push({cells : []});  // Blank Line
    secRow += 2;
    rows.push({
      cells : [{style : 'H2', marge : 'A'+secRow+':F'+secRow, value : fieldTypesMap['listview_condition'], 'align' : 'left'}]
      ,height : '14'
    });
    // 検索条件
    rows.push({
      cells : [
        {style : 'SectionLight', value : 'NO', width : '7.5', 'align' : 'center'},
        {style : 'SectionLight', value : fieldTypesMap['column_name'], width : '32.5'},
        {style : 'SectionLight', value : fieldTypesMap['api_name'], width : '32.5'},
        {style : 'SectionLight', value : fieldTypesMap['operator'], width : '24'},
        {style : 'SectionLight', value : fieldTypesMap['value'], width : '24'},
        {style : 'SectionLight', value : fieldTypesMap['note'], width : '45'}
      ], height : '14'
    });
    secRow++;

    var hasCondition = false;
    if(listview.describe.scope == 'mine' || listview.describe.scope == 'mru') {
      //スコープ
      var valLabel = fieldTypesMap[listview.describe.scope] + objLabelMap[sobjectName.toUpperCase()];
      rows.push({
        cells : [
          {style : 'Cell', value : '-', width : '7.5', 'align' : 'center'},
          {style : 'Cell', value : fieldTypesMap['scope']},
          {style : 'Cell', value : 'SCOPE'},
          {style : 'Cell', value : fieldTypesMap['operator_equals']},
          {style : 'Cell', value : valLabel},
          {style : 'Cell', value : ''}
        ], height : '14'
      });
      secRow++;
      hasCondition = true;
    }
    var whereCondition = _getConditons(listview.describe.whereCondition, 1);
    for(var c = 0; c < whereCondition.conditions.length; c++) {
      var condition = whereCondition.conditions[c];
      var fieldNames = condition.field.split('.');
      var field = _getFieldDesribe(sobjectName, condition.field);
      describe = (field && field.describe) ? field.describe : {};
      var fieldLabel = (fieldNames.length == 1) ? describe.label : (field.label + '.' + describe.label);
      var values = [];
      var conditionValues = condition.values;
      if(!condition.values) {
        var val = condition.subQuery ? condition.subQuery : '';
        conditionValues = [val];
      }
      for(var v = 0; v < conditionValues.length; v++) {
        var val = conditionValues[v];
        if(val.startsWith('\'')) {
          val = val.replace('\'', '');
          val = T_Util.replaceLast(val, '\'', '');
        }
        values.push(val);
      }
      var conjunction = '';
      if(c == 0 && whereCondition.conjunction) {
        conjunction = fieldTypesMap['conjunction'] + ': ' + whereCondition.conjunction;
      } else if(condition.conjunction){
        conjunction = condition.conjunction;
      }
      if(whereCondition.conditions.length <= 1) conjunction = ''; //一个条件的时候不输出

      var operatorKey = condition.operator.toLowerCase();
      if(operatorKey == 'like' && !values[0].startsWith('%') && values[0].endsWith('%')) {
        // StartWith
        operatorKey = 'startswith';
      }
      if(['like', 'notlike', 'startswith'].indexOf(operatorKey) >= 0) {
        values[0] = values[0].replace('\%', '');
        values[0] = T_Util.replaceLast(values[0], '\%', '');
      }
      operatorKey = 'operator_' + operatorKey;
      //'operator_startwith' : '次の文字列で始まる',
      //条件ロジック
      rows.push({
        cells : [
          {style : (condition.conjunction == 'OR') ? 'CellLight' : 'Cell', value : (c+1), width : '7.5', 'align' : 'center'},
          {style : (condition.conjunction == 'OR') ? 'CellLight' : 'Cell', value : fieldLabel},
          {style : (condition.conjunction == 'OR') ? 'CellLight' : 'Cell', value : condition.field},
          {style : (condition.conjunction == 'OR') ? 'CellLight' : 'Cell', value : fieldTypesMap[operatorKey]},
          {style : (condition.conjunction == 'OR') ? 'CellLight' : 'Cell', value : values.join(', ')},
          {style : (condition.conjunction == 'OR') ? 'CellLight' : 'Cell', value : conjunction}
        ], height : '14'
      });
      secRow++;
      hasCondition = true;
    }
    // 検索条件なし
    if(hasCondition == false) {
      secRow++;
      rows.push({
        cells : [
          {style : 'CellLight', value : fieldTypesMap['none'], 'align' : 'left'},
          {style : 'CellLight', value : '', width : '32.5'},
          {style : 'CellLight', value : '', width : '32.5'},
          {style : 'CellLight', value : '', width : '24'},
          {style : 'CellLight', value : '', width : '24'},
          {style : 'CellLight', marge : 'A'+secRow+':F'+secRow, value : '', width : '45'}
        ], height : '14'
      });
    }
    // 表示項目
    rows.push({cells : []});  // Blank Line
    secRow += 2;
    rows.push({
      cells : [{style : 'H2', marge : 'A'+secRow+':F'+secRow, value : fieldTypesMap['listview_column'], 'align' : 'left'}]
      ,height : '14'
    });
    rows.push({
      cells : [
        {style : 'SectionLight', value : 'NO', width : '7.5', 'align' : 'center'},
        {style : 'SectionLight', value : fieldTypesMap['column_name'], width : '32.5'},
        {style : 'SectionLight', value : fieldTypesMap['api_name'], width : '32.5'},
        {style : 'SectionLight', value : fieldTypesMap['data_type'], width : '24'},
        {style : 'SectionLight', value : fieldTypesMap['sort'], width : '24'},
        {style : 'SectionLight', value : fieldTypesMap['note'], width : '45'}
      ], height : '14'
    });
    secRow++;
    var columns = listview.describe.columns;
    for(var c = 0; c < columns.length; c++) {
      var column = columns[c];
      if(column.hidden == true) continue;
      var field = _getFieldDesribe(sobjectName, column.fieldNameOrPath);
      describe = (field && field.describe) ? field.describe : {};
      var fieldNames = column.fieldNameOrPath.split('.');
      var fieldLabel = (fieldNames.length == 1) ? describe.label : (field.label + '.' + describe.label);

      var fieldTypeLabel = self.getFieldType(describe, fieldTypesMap);
      if(describe.type == 'reference' && !T_Util.isBlank(describe.referenceTo)) {
        var refProp = self.getFieldReferenceProp(describe, objLabelMap);
        fieldTypeLabel += ' ('+refProp.refLabels.join(',')+')';
      }
      rows.push({
        cells : [
          {style : 'Cell', value : (c+1), width : '7.5', 'align' : 'center'},
          {style : 'Cell', value : fieldLabel},
          {style : 'Cell', value : column.fieldNameOrPath},
          {style : 'Cell', value : fieldTypeLabel},
          {style : 'Cell', value : T_Util.isNotBlank(column.sortDirection) ? fieldTypesMap[column.sortDirection] : ''},
          {style : 'Cell', value : ''}
        ], height : '14'
      });
    }

    rows.push({cells : []});  // Blank Line
    rows.push({cells : []});  // Blank Line
    return rows;
  }   // end of function _outputListViewRows

  // Outline
  var outlineSheet = {};
  outlineSheet['rows'] = [];
  outlineSheet['rows'].push({
    cells : [
      {style : 'Header', value : 'NO', width : '7.5', 'align' : 'center'},
      {style : 'Header', value : fieldTypesMap['listview_label'], width : '32.5'},
      {style : 'Header', value : fieldTypesMap['listview_apiname'], width : '32.5'},
      {style : 'Header', value : fieldTypesMap['object_name'], width : '32.5'},
      {style : 'Header', value : fieldTypesMap['object_apiname'], width : '32.5'},
    ], height : '17'
  });
  var excelData = [];
  var sheetNames = [];
  var lIndex = 0;
  for(var i = 0; i < sObjectNames.length; i ++){
    var sObjectName = sObjectNames[i];
    var sheet = {};
    var childViews = listviews[sObjectName];
    var startRow = 1;
    sheet['rows'] = [];
    for(var l = 0; l < childViews.length; l++) {
      var listview = childViews[l];
      var rows = _outputListViewRows(sObjectName, listview, startRow);
      sheet['rows'] = sheet['rows'].concat(rows);
      startRow += rows.length;
      // Listview Outline
      if(outlineSheet != null) {
        outlineSheet['rows'].push({
          cells : [
            {style : 'No', value : (lIndex+1), 'align' : 'center'},            //NO
            {style : 'Cell', value : listview.label},     //ListView Label
            {style : 'Cell', value : listview.developerName},    //ListView Developer Name
            {style : 'Cell', value : objFieldMap[sObjectName].label},     //Object Label
            {style : 'Cell', value : sObjectName},     //Object API Name
          ], height : '17'
        });
        lIndex++;
      }
    }
    
    excelData.push(sheet);
    sheetNames.push(objFieldMap[sObjectName].label);
    
  }

  if(outlineSheet != null) {
    // Insert to first
    excelData.unshift(outlineSheet);
    sheetNames.unshift(fieldTypesMap['listviews']);
  }
  
  //log('>>>> excelData ' + JSON.stringify(excelData));
  //log('>>>> sheetNames ' + JSON.stringify(sheetNames));
  var fileName = 'Salesforce List Views';
  if(sheetNames.length == 1) {
    //只有一个Object
    fileName = sheetNames;
  }
  // Fix name too long bug
  var newSheetNames = [];
  for(var i=0; i<sheetNames.length; i++) {
    var sheetName = sheetNames[i];
    if(sheetName.length > 30) {
      // 不能超过31
      var parts = sheetName.split('__');
      if(parts.length > 2){
        // Package Object
        sheetName = parts[(parts.length - 2)] + '__' + parts[(parts.length - 1)];
      }
      sheetName = sheetName.substr(0, 30);
    }
    sheetName = sheetName.replace(/(\/)|(\\)|(\[)|(\])|(\*)|(\:)|(\?)/g, '');
    if(newSheetNames.indexOf(sheetName) >= 0) {
      // Sheetname重複エラー
      sheetName = sheetName + '2';
    }
    newSheetNames.push(sheetName);
  }
  self.downloadExcel(excelData, newSheetNames, fileName);

  // 処理完了
  if(typeof options.callback == 'function') {
    options.callback();
  }
}

/** Export object reference as Excel file */
SD_DocGenerator.prototype.downloadExcel = function(data, sheetNames, fileName) {
  var styleBorder = { top: { style: "thin" }, left: { style: "thin" }, bottom: { style: "thin" }, right: { style: "thin" }};
  var fontSize = 12;

  XlsxPopulate
  .fromBlankAsync()
  .then(function(workbook) {
    // Create sheets
    for(var i = 0; i < sheetNames.length; i++) {
      if(i == 0) {
        workbook.sheet(i).name(sheetNames[i]);
      } else {
        workbook.addSheet(sheetNames[i]);
      }
    }
    for (var i = 0; i < data.length; i++) {
      var sheet = workbook.sheet(i);
      for (var j = 0; j < data[i].rows.length; j++) {
        var row = sheet.row(j+1); // Row 1~
        var rowData = data[i].rows[j];
        if(T_Util.isNotBlank(rowData.height)) {
          row.height(parseInt(rowData.height));
        }
        for (var k = 0; k < rowData.cells.length; k++) {
          // Cell setting
          var columnNo = T_Util.getColumn(k+1); // A
          var cellNo = columnNo + (j + 1);  // A1
          var cell = sheet.cell(cellNo);
          var column = sheet.column(columnNo);
          var cellData = rowData.cells[k];
          var dataValue = cellData.value || '';
          var styles = { 'border' : styleBorder, 'fontSize' : fontSize, 'verticalAlignment' : 'center' };
          if(cellData.style == 'Header') {
            styles['fill'] = '2a94d5';
            styles['fontColor'] = 'ffffff';
          }
          if(cellData.style == 'RelateHeader') {
            styles['fill'] = '8370c2';
            styles['fontColor'] = 'ffffff';
          }
          if(cellData.style == 'Section') {
            styles['fill'] = '747e96';
            styles['fontColor'] = 'ffffff';
          }
          if(cellData.style == 'ObjectLight') {
            styles['fill'] = 'acb9ca';
            styles['fontColor'] = '000000';
          }
          if(cellData.style == 'SectionGray') {
            styles['fill'] = 'bfbfbf';
            styles['fontColor'] = 'ffffff';
          }
          if(cellData.style == 'SectionLight') {
            styles['fill'] = 'acb9ca';
            styles['fontColor'] = 'ffffff';
          }
          if(cellData.style == 'RelateSectionLight') {
            styles['fill'] = '9F96BD';
            styles['fontColor'] = 'ffffff';
          }
          if(cellData.style == 'Cell') {
            styles['fill'] = 'ffffff';
            styles['fontColor'] = '000000';
          }
          if(cellData.style == 'CellLight') {
            styles['fill'] = 'EDEDED';
            styles['fontColor'] = '000000';
          }
          if(cellData.style == 'CellGray') {
            styles['fill'] = 'D9D9D9';
            styles['fontColor'] = '000000';
          }
          if(cellData.style == 'CellNoBorder') {
            styles['border'] = {};
          }
          if(cellData.style == 'H2') {
            styles['fontSize'] = '12';
            styles['bold'] = true;
            styles['border'] = false;
          }
          if(cellData.style == 'Buttons') {
            styles['fill'] = 'D9D9D9';
            styles['fontColor'] = '606060';
          }
          if(T_Util.isNotBlank(cellData.align)) {
            styles['horizontalAlignment'] = cellData.align; // center
          }
          if(T_Util.isNotBlank(cellData.width)) {
            column.width(parseInt(cellData.width));
          }
          if(T_Util.isNotBlank(cellData.marge)) {
            sheet.range(cellData.marge).merged(true);
          }
          cell.value(dataValue).style(styles);
        }
      }
    }
    return workbook.outputAsync();
  })
  .then(function(blob) {
    var link = document.createElement('A');
    link.href = window.URL.createObjectURL(blob);
    link.download = fileName + '.xlsx';;
    link.target = '_blank';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  });
}

// ER図 Encode64
SD_DocGenerator.prototype.getPackageName = function(sObjectName) {
  var parts = sObjectName.split('__');
  if(parts.length > 2){
    return parts[0];
  }
  return null;
}

//objFieldMetaUpMap
SD_DocGenerator.prototype.getRelatedObjMeta= function(objMetaMap, relatedName) {
  var parts = relatedName.split('.');
  if(parts.length > 1){
    // "SM_WarehouseNotice__c.Account__c"
    var key = parts[0].toUpperCase();
    if(objMetaMap.hasOwnProperty(key)) {
      return objMetaMap[key];
    }
  } else {
    // "RelatedContactList"
    var objName = T_Util.getRelatedObjectName(relatedName);
    if(objName == null) return null;
    var key = objName.toUpperCase();
    if(objMetaMap.hasOwnProperty(key)) {
      return objMetaMap[key];
    }
  }
  return null;
}
