SD_DocGenerator.prototype.encode64 = function(data) {
  var self = this;
  r = "";
  for (i=0; i<data.length; i+=3) {
    if (i+2==data.length) {
      r +=self.append3bytes(data.charCodeAt(i), data.charCodeAt(i+1), 0);
    } else if (i+1==data.length) {
      r += self.append3bytes(data.charCodeAt(i), 0, 0);
    } else {
      r += self.append3bytes(data.charCodeAt(i), data.charCodeAt(i+1), data.charCodeAt(i+2));
    }
  }
  return r;
}

SD_DocGenerator.prototype.append3bytes = function(b1, b2, b3) {
  var self = this;
  c1 = b1 >> 2;
  c2 = ((b1 & 0x3) << 4) | (b2 >> 4);
  c3 = ((b2 & 0xF) << 2) | (b3 >> 6);
  c4 = b3 & 0x3F;
  r = "";
  r += self.encode6bit(c1 & 0x3F);
  r += self.encode6bit(c2 & 0x3F);
  r += self.encode6bit(c3 & 0x3F);
  r += self.encode6bit(c4 & 0x3F);
  return r;
}

SD_DocGenerator.prototype.encode6bit = function(b) {
  if (b < 10) {
   return String.fromCharCode(48 + b);
  }
  b -= 10;
  if (b < 26) {
   return String.fromCharCode(65 + b);
  }
  b -= 26;
  if (b < 26) {
   return String.fromCharCode(97 + b);
  }
  b -= 26;
  if (b == 0) {
   return '-';
  }
  if (b == 1) {
   return '_';
  }
  return '?';
}


// Get field type label from metadata
SD_DocGenerator.prototype.getFieldType = function(field, fieldTypesMap) {
  if(T_Util.isBlank(field)) return null;
  var fieldType = field.type;
  if(field.calculated == 'true') {
    if(field.byteLength == '0' && T_Util.isBlank(field.calculatedFormula)) {
      // Roll-Up Summary
      fieldType = 'rollupsummary';
    } else {
      fieldType = 'formula';
    }
  }
  if(field.type == 'string'){
    if(field.autoNumber == 'true'){
      fieldType = 'autonumber';
    }
  }
  
  if(field.type == 'textarea') {
    fieldType = field.extraTypeInfo;
  }
  if(field.type == 'reference') {
    if(T_Util.isNotBlank(field.relationshipOrder)) {
      fieldType = 'masterdetail';
    } else {
      fieldType = 'lookup';
    }
  }
  if(field.type == 'double' && field.calculated !== 'true'){
    fieldType = 'number';
    if(field.displayLocationInDecimal == 'true') {
      fieldType = 'geolocation';
    }
  }

  //log('>>>> fieldType  ' + fieldType );
  var fieldTypeLabel = fieldType;
  if(fieldTypesMap.hasOwnProperty(fieldType)){
    fieldTypeLabel = fieldTypesMap[fieldType];
  }
  if(fieldType == 'int' || fieldType == 'number' || fieldType == 'double') {
    fieldTypeLabel += ' ('+(field.precision - field.scale)+', '+field.scale+')'
  }
  // Formula
  if(fieldType == 'formula' && fieldTypesMap.hasOwnProperty(field.type)) {
    fieldTypeLabel += ' ('+fieldTypesMap[field.type]+')'
  }
  return fieldTypeLabel;
}

// Get field reference label and master objects
// return { refLabels : [], masters : [] }
SD_DocGenerator.prototype.getFieldReferenceProp = function(field, objLabelMap) {
  var referenceLabels = [];
  var masters = [];
  if(Array.isArray(field.referenceTo)) {
    // Multiple References
    var references = field.referenceTo;
    for(var ri = 0; ri < references.length; ri ++) {
      var referenceToUpper = references[ri].toUpperCase();
      if(objLabelMap.hasOwnProperty(referenceToUpper)){
        referenceLabels.push(objLabelMap[referenceToUpper]);
        if(typeof field.relationshipOrder != 'undefined') {
          masters.push(objLabelMap[referenceToUpper]);
        }
      }
    }
  } else {
    var referenceToUpper = field.referenceTo.toUpperCase();
    if(objLabelMap.hasOwnProperty(referenceToUpper)){
      referenceLabels.push(objLabelMap[referenceToUpper]);
      if(typeof field.relationshipOrder != 'undefined') {
        masters.push(objLabelMap[referenceToUpper]);
      }
    }
  }
  return { refLabels : referenceLabels, masters : masters };
}

