/**
 * Show Salesforce Objects Search Result
 * @param  {}
 */
SD_PopupView.prototype.resetNaviHeight = function(options){
  var totalHeight = 0;

  $("#container > .nav").children().each(function(){
    totalHeight = totalHeight + $(this).outerHeight(true);
  });
  $("#container > .nav").height(totalHeight);
}

