/**
 * Show Search Result
 * @param  {}
 */
SD_PopupView.prototype.showSfdcObjectResult = function(options){
	var self = this;
	var results = options.results || [];
  var url = options.salesforceURL || '';
  if(options.isLEX || T_Util.isBlank(url)) {
    url = 'https://' + T_Util.getParser(options.url).hostname;
  }
	$('#container-sfdc-objects').find('.li-search-result').remove();
	if(!$('#li-no-result').hasClass('hide')){
		$('#li-no-result').addClass('hide');
	}
	if(results.length > 0){
		$('#nav-check-all').removeClass('hide');
	} else {
		$('#nav-check-all').addClass('hide');
	}

	for(var i = 0; i < results.length; i++){
		var sobject = results[i];
		var customCls = (sobject.custom) ? ' custom' : '';
    var checked = (self.selectedObjects.indexOf(sobject.name) >= 0) ? ' checked="checked"' : '';
    var newUrl, listUrl, settingUrl;
    if(options.isLEX) {
      newUrl = url + '/' + sobject.keyPrefix + '/e';
      listUrl = url + '/lightning/o/' + sobject.name + '/list';
      settingUrl = url + '/lightning/setup/ObjectManager/' + sobject.name + '/FieldsAndRelationships/view';
      if(sobject.custom && typeof sobject.objectId != 'undefined'){
        settingUrl = url + '/lightning/setup/ObjectManager/' + sobject.objectId + '/FieldsAndRelationships/view';
      }
    } else {
      // Classic
      newUrl = url + '/' + sobject.keyPrefix + '/e';
      listUrl = url + '/' + sobject.keyPrefix;
      settingUrl = url + '/p/setup/layout/LayoutFieldList?type=' + sobject.name + '&setupid=' + sobject.name + 'Fields';
      if(sobject.custom && typeof sobject.objectId != 'undefined'){
        settingUrl = url + '/' + sobject.objectId;
      }
    }

		var html = '<li role="presentation" class="li-search-result'+customCls+'">';
		html += '<div class="checkbox checkbox-primary"><input type="checkbox" '+checked+' value="'+sobject.name+'" id="chk_'+sobject.name+'"><label for="chk_'+sobject.name+'">&nbsp;</label></div>';
		html += '<div class="sobject-label">'+sobject.label+'&nbsp;<br /><span class="small">'+sobject.name+'</span></div>';
		html += '<div class="sobject-tools text-right">';
		html += '<a href="' + newUrl + '" target="_blank" class="btn btn-default btn-xs flat" title="New" data-container="body" data-toggle="tooltip" data-placement="top"><i class="fa fa-plus"></i></a>';
		html += '<a href="' + listUrl + '" target="_blank" class="btn btn-default btn-xs flat ml5" title="List" data-container="body" data-toggle="tooltip" data-placement="top"><i class="fa fa-list-ul"></i></a>';
		html += '<a href="'+settingUrl+'" target="_blank" class="btn btn-default btn-xs flat ml5" title="Setting" data-container="body" data-toggle="tooltip" data-placement="top"><i class="fa fa-cog"></i></a>';
    if(sobject.custom != true){
			//标准Object
			var referenceUrl = 'https://developer.salesforce.com/docs/atlas.en-us.object_reference.meta/object_reference/sforce_api_objects_'+sobject.name.toLowerCase()+'.htm#topic-title';
			html += '<a href="'+referenceUrl+'" target="_blank" class="btn btn-default btn-xs flat ml5" title="Object Reference"><i class="fa fa-question-circle"></i></a>';
			$('#btn-object-reference').removeClass('hide').attr('href', referenceUrl);
    }
    html += '<a href="#" class="btn btn-editor btn-xs flat ml5 btn-object-query-editor" data-name="'+sobject.name+'" title="Query Editor" data-container="body" data-toggle="tooltip" data-placement="top">';
    html += '<i class="fa fa-database"></i></a>';
    html += '<a href="#" class="btn btn-editor btn-xs flat ml5 btn-object-field-editor" data-name="'+sobject.name+'" title="Object Fields" data-container="body" data-toggle="tooltip" data-placement="top">';
    html += '<i class="fa fa-th-large"></i></a>';
    
		$('#container-sfdc-objects').find('.nav-search-result').first().append(html);
		$('#container-sfdc-objects').find('.li-search-result :checkbox').bind('click', function() {
	    self.checkObject({checkbox : this});
    });
    $('[data-toggle="tooltip"]').tooltip();
  }
}
//未输入任何Keyword
SD_PopupView.prototype.sfdcObjectNoResult = function(options){
	$('#container-sfdc-objects').find('.li-search-result').remove();
  var hintTxt = 'Type in any sobject name or sobject label to filter Salesforce Objects.<br />';
  hintTxt += 'e.g. type <code>__c</code> to filter all custom objects.<br /><br />';
  $('#li-no-result').removeClass('hide').html(hintTxt);
  // Restore search result
  $('#nav-check-all').addClass('hide');
  $('#chk-all-objects').prop('checked', false);
  this.resetLabel();
  
}
//未找到任何Object
SD_PopupView.prototype.sfdcObjectNotFound = function(options){
	$('#container-sfdc-objects').find('.li-search-result').remove();
  $('#li-no-result').removeClass('hide').html('No object was found.');
  // Restore search result
  $('#nav-check-all').addClass('hide');
  $('#chk-all-objects').prop('checked', false);
  this.resetLabel();
}
//选中全部
SD_PopupView.prototype.checkAll = function(options){
  var self = this;
	var checked = $('#chk-all-objects').is(':checked');
  $('#container-sfdc-objects').find('.li-search-result').each(function(){
    var $chk = $(this).find(':checkbox').first();
    var value = $chk.val();
    $chk.prop("checked",checked);
    if(checked == true) {
      if(self.selectedObjects.indexOf(value) < 0) {
        self.selectedObjects.push(value);
      }
    } else {
      if(self.selectedObjects.indexOf(value) >= 0) {
        var index = self.selectedObjects.indexOf(value);
        self.selectedObjects.splice(index, 1);
      }
    }
  });
  this.resetLabel();
  //log('>>>> T_SelectedObjects ' + JSON.stringify(T_SelectedObjects));
}
SD_PopupView.prototype.checkObject = function(options){
  var self = this;
	var $chk = $(options.checkbox);
	var value = $chk.val();
  if($chk.is(':checked') == true) {
    if(self.selectedObjects.indexOf(value) < 0) {
      self.selectedObjects.push(value);
    }
  } else {
    if(self.selectedObjects.indexOf(value) >= 0) {
      var index = self.selectedObjects.indexOf(value);
      self.selectedObjects.splice(index, 1);
    }
  }
  this.recheckAll();
}
// 重新验证是否全选
SD_PopupView.prototype.recheckAll = function(options){
	var checkAll = true;
	$('#container-sfdc-objects').find('.li-search-result').each(function(){
    var $chk = $(this).find(':checkbox').first();
    if(!$chk.is(':checked')) {
    	checkAll = false;
    	return false;
    }
  });
  $('#chk-all-objects').prop("checked",checkAll);
  this.resetLabel();
}

// 重设标签
SD_PopupView.prototype.resetLabel = function(options){
	var label;
	if(this.selectedObjects.length > 0) {
		label = '<span class="label label-light-primary">';
    //$('#btn-object-export-erd').removeClass('disabled').removeAttr('disabled');
		$('#btn-export-all-dropdown').removeClass('disabled').removeAttr('disabled');
	} else {
		label = '<span class="label label-warning">';
    //$('#btn-object-export-erd').addClass('disabled').attr('disabled', 'disabled');
		$('#btn-export-all-dropdown').addClass('disabled').attr('disabled', 'disabled');
	}
	label += this.selectedObjects.length + ' Selected</span>';
	$('#nav-check-all').find('.check-all-label').first().html(label);
}

// 显示Debug Log面板
SD_PopupView.prototype.showLogViewer = function(options){
  options = options || {};
  var self = this;
  var sendRequest = options.sendRequest;
  if(!$('#loading').hasClass('hide')){
		$('#loading').addClass('hide');
  }
  // Show debuglog menu
  $('.jq-debuglog-viewer').removeClass('hide');
  $('#container').removeClass('hide');

  //显示LogViewer页面
  self.showDebugLogViewPage({ 
    returnCallback : function() {
      sendRequest({ mode : 'resizePanel', height : self.getContainerHeight() });
    } 
  });
  var viewerHtml = self.getLogViewerHtml(options);
  var $panel = jQuery('#container-debuglog-viewer');
  $panel.empty().append(viewerHtml);
  // 关闭/打开面板
  $panel.find('.collapse').click(function(){
    var $menu = jQuery(this).closest('.panel-header');
    var $panel = jQuery(this).closest('.panel-header').siblings('.panel-event').first();
    if($menu.hasClass('open')) {
      // Close
      $menu.removeClass('open');
      $panel.hide();
    } else {
      $menu.addClass('open');
      $panel.show();
    }
    sendRequest({ mode : 'resizePanel', height : self.getLogViewerHeight() });
  });

  // Check All
  var handleCheck = function(options) {
    options['mode'] = 'taggleDebugLog';
    sendRequest(options);
  }

  $('#logviewer-checkbox-all').click(function(){
    var $checkbox = jQuery(this);
    var checked = $checkbox.is(':checked');
    var targets = [];
    $panel.find('.checkbox-category, .checkbox-event').each(function(){
      var $menu = jQuery(this);
      if(checked != $menu.is(':checked')) {
        //$menu.trigger('click');
        $menu.prop('checked', checked);
      }
      if($menu.hasClass('checkbox-category')) {
        targets.push($menu.data('category'));
      }
    });
    // 全选时取消选中debug only
    if(checked) $('#logviewer-checkbox-debug-only, #logviewer-checkbox-query-only').prop('checked', false);
    // Send request to page
    handleCheck({ categories : targets, checked : checked });
  });
  // 只显示debuglog / query
  $('#logviewer-checkbox-debug-only, #logviewer-checkbox-query-only').click(function(){
    var $checkbox = jQuery(this);
    var checked = $checkbox.is(':checked');
    if(checked){
      // 取消全选
      $('#logviewer-checkbox-all').prop('checked', false);
      if($checkbox.attr('id') == 'logviewer-checkbox-debug-only') {
        $('#logviewer-checkbox-query-only').prop('checked', false);
      } else {
        $('#logviewer-checkbox-debug-only').prop('checked', false);
      }
    } else {
      return;
    }
    // 选中时只显示debuglog
    $panel.find('.checkbox-category, .checkbox-event').each(function(){
      var $menu = jQuery(this);
      if($menu.is(':checked')) {
        // 取消选中
        $menu.prop('checked', false);
        //$menu.trigger('click');
      }
    });
    var tag = $checkbox.data('tag');
    var cate = $checkbox.data('category');
    $panel.find('.checkbox-event[data-tag="'+tag+'"]').prop('checked', checked);
    //$panel.find('.checkbox-event[data-tag="'+tag+'"]').trigger('click');
    // 开关menu
    var isOpened = false;
    $panel.find('.panel-header.open').each(function(){
      $menu = jQuery(this);
      $cates = $menu.find('.category-menu[data-category="'+cate+'"]');
      if($cates.length > 0) {
        // 已经打开状态，不需要另外打开
        isOpened = true;
        return;
      }
      // 关闭其他menu
      var $collapse = $menu.find('.collapse');
      $collapse.trigger('click');
    });
    if(!isOpened) {
      // 打开对象menu
      $panel.find('.category-menu[data-category="'+cate+'"]').siblings('.collapse').trigger('click');
    }
    // Hide Other Line
    var targets = [];
    $panel.find('.checkbox-category').each(function(){
      targets.push(jQuery(this).data('category'));
    });
    handleCheck({ categories : targets, checked : false });
    // Send request to page
    handleCheck({ tags : [tag], checked : checked });
  });
  // Category Menu
  $panel.find('.category-menu').click(function(){
    // Store first
    var lineNo = $(this).attr('data-line');
    $panel.find('.category-menu.on, .event-menu.on').removeClass('on');
    $panel.find('.category-menu.on, .event-menu.on').removeAttr('data-line');
    if(T_Util.isBlank(lineNo)) {
      lineNo = '1';
    } else {
      // Next line
      lineNo = parseInt(lineNo) + 1;
    }
    $(this).addClass('on').attr('data-line', lineNo);
    var debugCate = jQuery(this).data('category');
    sendRequest({ mode : 'highlightDebugLog', category : debugCate, line : lineNo });
  });
  // Category Checkbox
  $panel.find('.checkbox-category').click(function(){
    var $checkbox = jQuery(this);
    var checked = $checkbox.is(':checked');
    // 子菜单
    var $tagChks = $checkbox.closest('li').find('.checkbox-event');
    $tagChks.prop('checked', checked);
    var targets = [];
    $tagChks.each(function(){
      targets.push($(this).data('tag'));
    });
    // Send request to page
    handleCheck({ tags : targets, checked : checked });
  });
  // Event Checkbox
  $panel.find('.checkbox-event').click(function(){
    var $checkbox = $(this);
    // Send request to page
    handleCheck({ tags : [$checkbox.data('tag')], checked : $checkbox.is(':checked') });
  });
  // Event Menu
  $panel.find('.event-menu').click(function(){
    // Store first
    var lineNo = $(this).attr('data-line');
    $panel.find('.category-menu.on, .event-menu.on').removeClass('on');
    $panel.find('.category-menu.on, .event-menu.on').removeAttr('data-line');
    if(T_Util.isBlank(lineNo)) {
      lineNo = '1';
    } else {
      // Next line
      lineNo = parseInt(lineNo) + 1;
    }
    $(this).addClass('on').attr('data-line', lineNo);
    var debugTag = jQuery(this).data('tag');
    sendRequest({ mode : 'highlightDebugLog', tag : debugTag, line : lineNo });
  });
}

/**
 * DebugLog detail panel
 * @param  options : {}
 */
SD_PopupView.prototype.getLogViewerHtml = function(options){
  options = options || {};
  var master = options.master || {};
  var lineTags = options.lineTags || {};
  var html = '<div class="panel-head"><div class="panel-head-checkbox"><div class="checkbox checkbox-primary"><input type="checkbox" id="logviewer-checkbox-all" checked><label for="logviewer-checkbox-all">All</label></div></div>';
  html += '<div class="panel-head-checkbox"><div class="checkbox checkbox-primary"><input type="checkbox" id="logviewer-checkbox-debug-only" data-tag="USER_DEBUG" data-category="apexcode"><label for="logviewer-checkbox-debug-only">Debug Only</label></div></div>';
  html += '<div class="panel-head-checkbox"><div class="checkbox checkbox-primary"><input type="checkbox" id="logviewer-checkbox-query-only" data-tag="SOQL_EXECUTE_BEGIN" data-category="db"><label for="logviewer-checkbox-query-only">Query Only</label></div></div>';
  html += '</div>';
  html += '<ul class="panel-category">';
  var categories = Object.keys(master);
  var debugTags = Object.keys(lineTags);
  //console.log('>>>> master', master);
  //console.log('>>>> lineTags', lineTags);
  for(var i = 0; i < categories.length; i++) {
    var ckey = categories[i];
    var category = master[ckey];
    var events = category.events;
    var open = (i == 0) ? 'open' : '';
    var show = (i == 0) ? 'display: block;' : '';
    cHtml = '<li><div class="panel-header '+open+'"><div class="checkbox checkbox-primary"><input type="checkbox" class="checkbox-category" id="checkbox-category-'+i+'" data-category="'+ckey+'" checked><label for="checkbox-category-'+i+'">&nbsp;</label></div>';
    cHtml += '<a href="javascript:void(0);" class="category-menu" data-category="'+ckey+'">'+category.label+'</a><a href="javascript:void(0);" class="collapse pull-right"><i class="fa fa-angle-down"></i></a></div><ul class="panel-event" style="'+show+'">';
    var existEvent = false;
    for(var k = 0; k < events.length; k++) {
      if(debugTags.indexOf(events[k]) < 0) continue;
      cHtml += '<li><div class="checkbox checkbox-primary"><input type="checkbox" class="checkbox-event" id="checkbox-event-'+i+'-'+k+'" data-tag="'+events[k]+'" checked><label for="checkbox-event-'+i+'-'+k+'">&nbsp;</label></div>';
      cHtml += '<a href="javascript:void(0);" class="event-menu" data-tag="'+events[k]+'">'+events[k]+'</a></li>';
      existEvent = true;
    }
    cHtml += '</ul></li>';
    html += (existEvent) ? cHtml : '';
  }
  // Other log lines
  html += '<li><div class="panel-header"><div class="checkbox checkbox-primary"><input type="checkbox" class="checkbox-category" id="checkbox-category-other" data-category="other" checked><label for="checkbox-category-other">&nbsp;</label></div>';
  html += '<a href="javascript:void(0);" class="category-menu" data-category="other">Other</a></div></li>';
    
  html += '</ul>';
  return html;
}

/**
 * Show DebugLog Result
 * @param  {}
 */
SD_PopupView.prototype.showDebugLogResult = function(options){
	var self = this;
	var results = options.results || [];
  var url = options.salesforceURL || '';
  if(options.isLEX || T_Util.isBlank(url)) {
    url = 'https://' + T_Util.getParser(options.url).hostname;
  }

	$('#container-debuglog-search').find('.li-search-result').remove();
	if(!$('#li-no-result').hasClass('hide')){
		$('#li-no-result').addClass('hide');
  }
  // No Result
  var $noResult = $('#container-debuglog-search #li-no-result');
  if(results.length == 0) {
    var hintTxt = 'Opps! There has no debug log found.';
    $noResult.removeClass('hide').html(hintTxt);
    return;
  } else if(!$noResult.hasClass('hide')){
		$noResult.addClass('hide');
  }
  // Sort Desc by LastModifiedDate
  results = results.sort(function(a, b){
    var x = moment(a.LastModifiedDate);
    var y = moment(b.LastModifiedDate);
    if (x.isBefore(y)) return 1;
    if (x.isAfter(y)) return -1;
    return 0;
  });

	for(var i = 0; i < results.length; i++){
    // Only show 5 debuglog
    //if(i >= 5) break;
    var apexlog = results[i];
    var logUrl = url + '/p/setup/layout/ApexDebugLogDetailEdit/d?setupid=ApexDebugLogs&apex_log_id=' + apexlog.Id;
    if(!T_Util.isBlank(apexlog.Line)) {
      logUrl = logUrl + '#' + apexlog.Line;
    }
    var userUrl = url + '/' + apexlog.LogUser.Id;
    // 2018-12-08T13:38:55.000+0000
    var modifiedDate = moment(apexlog.LastModifiedDate).format("MM/DD HH:mm:ss");
    var html = '<li role="presentation" class="li-search-result">';
    html += '<div class="debuglog-name"><a href="' + userUrl + '" target="_blank">'+apexlog.LogUser.Name+'</a>,&nbsp;'+modifiedDate;
    html += '<a href="'+logUrl+'" target="_blank" class="btn btn-primary btn-xs flat pull-right debuglog-view">View</a></div>';
    html += '<div class="debuglog-url">'+apexlog.Operation+'</span></div>';
		html += '<div class="debuglog-body small">';
    html += apexlog.Body + '</div></li>';
    $('#container-debuglog-search').find('.nav-search-result').first().append(html);
  }
}