// App
var T_DEBUG_MODE = false;
var T_App = new SD_Popup({ debugmode : T_DEBUG_MODE });
var T_Util = new SD_Util();
var SF_API_VERSION = '53.0';
// Listener
chrome.runtime.onMessage.addListener(function(request, sender, sendResponse) {
  var bgtypes = ['SD_GaTrack', 'SD_opentab', 'SD_sfdcoauth'];
  if(request.type && bgtypes.indexOf(request.type) >= 0) {
    //console.log('>>> call for background');
  } else if(request.mode == 'CROB' && request.tabid) {
    // Handle CROB Request
    T_App.handleCrobRequest(request, sendResponse);
  } else if(T_App.pageCallback == null || typeof T_App.pageCallback.callback != 'function') {
    var tabid = request.tabid;
    if(tabid && tabid != T_App.tabid) {
      //console.log('>>> call for tabid' + tabid);
      return chrome.tabs.sendMessage(tabid, request);
    }
  } else{
    T_App.handle(request);
    return sendResponse(true);
  }
});
// Onload
window.onload = function() {
  T_App.run();
};